import './MenuItem.style.scss'
import React from 'react'
import { withRouter } from 'react-router-dom'

function MenuItem({ title, imageUrl, size, history, linkUrl, match }) {  //sort linkUrl
  // console.log(`${size}`)
  console.log(match.url)
  return (
    <div
      className={`${size} menu-item`} //"size" is for making men and woman photos/items bigger
      onClick={() => history.push(`${match.url}${linkUrl}`)}

    >
      <div className='mouseLayer' />
      
       <div
         style={{ background: `url(${imageUrl}) no-repeat center center/cover` }}
         className='bg-img'
       />

      <div className='content'>
        <h1 className='title'>{title.toUpperCase()}</h1>
        <span className='subtitle'> SHOP NOW</span>
      </div>
    </div>

  )
}

export default withRouter(MenuItem)
